import '../sass/main.scss';
import makeAnimated from 'react-select/animated';
import Select from 'react-select'
import { ApolloProvider } from "react-apollo";
import ApolloClient from "apollo-boost";
const client = new ApolloClient({
  uri: "https://api.miautohoy.com/graphql"
});
import { Query } from "react-apollo";
import { gql } from "apollo-boost";
import { AllBrandsQuery } from '../queries/tauto.queries';

const customStyles = {
  input: () => ({
    height: '50px',
    maxWidth: '20px',
  })
}

export default class FlavorForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = { brands: [] };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    this.setState({ selectedBrand: event.target.value });
  }

  handleSubmit(event) {
    alert('Your favorite flavor is: ' + this.state.value);
    event.preventDefault();
  }
  render() {

    return (
      <ApolloProvider client={client}>

        <div className="container text-center">
          <Query
            query={AllBrandsQuery}
          >
            {({ loading, error, data }) => {
              if (loading) return <p>Loading...</p>;
              if (error) return <p>Error :(</p>;
              this.setState({ brands: data })


              return <form onSubmit={this.handleSubmit}>
                <label>
                  Completa los datos para obtener el precio del veh√≠culo
          <Select placeholder="Selecciona la marca" components={makeAnimated()} options={this.state.brands} styles={customStyles} />
                </label>
                <button type="submit" className="btn-primary" value="Submit">Ver precio</button>
              </form>
            }}
          </Query>
        </div>
      </ApolloProvider>

    );
  }
}